import React from 'react';

import './Contact.css'

const Contact = (props) => (
  <div className="contact">
    <header className="contact__header">
      <h1>Skontaktuj się ze mną!</h1>
    </header>
    <div className="contact__card skype">
      <a href="skype:live:mateusz.wodnik?call" className="skype__logo">
        <span className="fab fa-skype"></span>
      </a>
      <a href="skype:live:mateusz.wodnik?call" className="skype__call">
        <span className="fas fa-phone"></span>
        <span>Zadzwoń do mnie!</span>
      </a>
    </div>
    <form className="contact__card form">
      <input type="text" className="form__item form__item--name" placeholder="Imię"/>
      <input type="text" className="form__item form__item--name" placeholder="Nazwisko"/>
      <input type="email" className="form__item form__item--email" placeholder="adres@email.pl"/>
      <input type="text" className="form__item form__item--subject" placeholder="Temat"/>
      <textarea className="form__item form__item--message" name="message" id="message" cols="30" rows="10" placeholder="Wiadomość"></textarea>
      <button className="form__item form__item--submit" type="submit">Wyślij</button>
    </form>
  </div>
);

export default Contact;



