import React, { Component } from 'react';

import './backup.css';

class About extends Component {
  constructor (props){
    super(props);
    this.state = {
      counter: 0,
    }
  }

  render() {
    return (
      <div className="about">
        <h2>About</h2>
      </div>
    );
  }
}

export default About;



